import React from "react";
import "../../App.css";
import "./Hobbies.css";
import lusageDuMonde from "../../images/lusageDuMonde.webp";
import lesSecretsDeLaMerRouge from "../../images/lesSecretsDeLaMerRouge.webp";
import lesCavaliers from "../../images/lesCavaliers.webp";
import moto1 from "../../images/moto1.webp";
import moto2 from "../../images/moto2.webp";
import moto3 from "../../images/moto3.webp";
import moto4 from "../../images/moto4.webp";
import moto5 from "../../images/moto5.webp";
import moto6 from "../../images/moto6.webp";
import rando1 from "../../images/rando1.webp";
import rando2 from "../../images/rando2.webp";
import rando3 from "../../images/rando3.webp";
import rando4 from "../../images/rando4.webp";
import rando5 from "../../images/rando5.webp";

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

const mada_img = [moto1, moto2, moto3, moto4, moto5, moto6];
const rando_img = [rando1, rando2, rando3, rando4, rando5];

export default function Hobbies() {
  return (
    <>
      <div className="hobbies-title-container">
        <h1 className="hobbies-title"> Lectures </h1>
      </div>
      <div className="moto_container">
        <ul>
          <li className="moto_li">
            Trs intress par l'actualit conomique, sociale et gopolitique,
            je suis abonn au monde diplomatique.
          </li>
          <li className="moto_li">
            Passionn de romans d'aventure, mes auteurs favorits ont vcu le
            sicle dernier. J'ai toujours  mes cts au moins un livre de
            Joseph Kessel, Nicolas Bouvier ou Henry de Montfreid.
          </li>
        </ul>
        <div className="hobbies-img-container">
          <img
            className="hobbies-img"
            src={lusageDuMonde}
            alt="L'Usage du Monde, Nicolas Bouvier"
          ></img>
          <img
            className="hobbies-img"
            src={lesSecretsDeLaMerRouge}
            alt="Les Secrets de la Mer Rouge, Henry de Montfreid"
          ></img>
          <img
            className="hobbies-img"
            src={lesCavaliers}
            alt="Les cavaliers, Joseph Kessel"
          ></img>
        </div>
      </div>
      <div className="hobbies-title-container">
        <h1 className="hobbies-title"> Podcasts</h1>
      </div>
      <div className="hobbies-text">
        {"\u2022 "}
        <a
          href="https://www.franceculture.fr/emissions/les-pieds-sur-terre"
          rel="noreferrer"
          target="_blank"
        >
          Les pieds sur Terre :
        </a>{" "}
        Emission sociologique de France Culture o les locuteurs dtaillent un
        fait marquant de leur vie. "Mon histoire d'amour durant le confinement",
        ou bien "Le jour o j'ai assist  un viol" en passant par "Comment j'ai
        dcouvert un trsor", tous les sujets sont abords, et sans tabout !
      </div>
      <div className="hobbies-text">
        {"\u2022 "}
        <a
          href="http://rendezvousavecmrx.free.fr/page/liste.php"
          target="_blank"
          rel="noreferrer"
        >
          Rendez-vous avec X :
        </a>{" "}
        Entretien par Patrick Pesnot de 'Monsieur X', ancien agent de la DGSE.
        Monsieur X revient sur des histoires d'espionnage et des affaires
        secrtes dans lesquelles se jouent l'histoire de nos dmocraties !
      </div>
      <div className="hobbies-text">
        {"\u2022 "}
        <a
          href="https://www.youtube.com/c/thinkerview"
          target="_blank"
          rel="noreferrer"
        >
          Thinkerview :
        </a>{" "}
        Chane YouTube recevant des journalistes, des diplomates, des lanceurs
        d'aleter etc .. Ils sont invits  s'exprimer sur des sujets
        d'actualit. Dans une socit o le temps est compt, ce format
        d'entretien procure une grande bouffe d'air frais, les intervenants
        pouvant prendre la parole jusqu' 4h sur le sujet qu'ils souhaitent !
      </div>
      <div className="hobbies-text">
        {"\u2022 "}
        <a
          href="https://open.spotify.com/show/6NsIUcTCFBIPT7TxP87zdF"
          target="_blank"
          rel="noreferrer"
        >
          Les Baladeurs :
        </a>{" "}
        Une srie 'Les others', des histoires et des aventures en pleine nature.
      </div>
      <div className="hobbies-title-container">
        <h1 className="hobbies-title"> Moto </h1>
      </div>
      <div className="moto_container">
        <ul>
          <li className="moto_li">
            J'ai achet ma premire moto  Madagascar, un peu par hasard.
            Antananarivo tant une ville atrocement embouteille, il tait
            ncessaire d'avoir un deux roues pour se dplacer. Donc c'est plutt
            par besoin que par passion que j'ai commenc  rouler.
          </li>
          <li className="moto_li">
            La suite est classique, je partais en week-end en moto avec des
            copains motards et la passion a t vite suscite. Finalement en
            2020,  la fin de mon contrat  Madagascar, je partais voyager tout
            seul avec ma bcane, pour faire le tour de l'le pendant les deux
            prochains mois.
          </li>
        </ul>
        <div className="carousel_container">
          <Carousel
            infiniteLoop={true}
            dynamicHeight={true}
            autoPlay={true}
            swipeable={true}
            showArrows={false}
            interval={5000}
          >
            {mada_img.map((img) => {
              return (
                <img
                  src={img}
                  alt="Moto  Madagascar"
                  className="moto_img"
                ></img>
              );
            })}
          </Carousel>
        </div>
      </div>
      <div className="hobbies-title-container">
        <h1 className="hobbies-title"> Randonne </h1>
      </div>
      <div className="rando_container">
        <div className="carousel_container">
          <Carousel
            infiniteLoop={true}
            dynamicHeight={true}
            autoPlay={true}
            swipeable={true}
            showArrows={false}
            interval={5000}
          >
            {rando_img.map((img) => {
              return (
                <img
                  src={img}
                  alt="Moto  Madagascar"
                  className="moto_img"
                ></img>
              );
            })}
          </Carousel>
        </div>
        <div className="rando_text">
          Passionn de voyages en nature, la France est un remarquable terrain
          de jeu ! Durant mes escapades, j'ai eu l'occasion de faire quelques
          tapes du GR5 (Les Vosges), du GR34 (La Bretagne), du GR10 (Les
          Pyrnes) et du GR20 (La Corse).
        </div>
      </div>
    </>
  );
}
