function ExperienceHeader(props) {
  return (
    <div className="columns">
      <div className="left">
        <h1>{props.date}</h1>
        <div></div>
      </div>
      <div className="right">
        <h1>{props.title}</h1>
      </div>
    </div>
  );
}

function TimeLine() {
  return (
    <div className="left">
      <div className="arrow">
        <i className="fas fa-long-arrow-alt-down fa-6x"></i>
      </div>
      <div className="timeline"></div>
    </div>
  );
}

